<template>
    <div class="home-wrapper">
        <div
            class="content-wrapper"
            ref="contentWrapper">
            <!-- 轮播banner组件 -->
<!--             <carousel
                v-if="bannerList.length > 0"
                :interval=3500
                :list="bannerList">
            </carousel> -->
            <!-- 列表部分list组件 -->
            <news-list
                :newsList='newsList'
                :lastListLen="lastListLen"
                :needTransition="!listFromCache">
            </news-list>
            <b-loading :show="showLoading"></b-loading>
            <infinite-loading v-if="!showLoading"
                spinner="spiral"
                :on-infinite="getMoreNews"
                ref="infiniteLoading">
                <span slot="no-more">
                  亲，已经拉到底啦
                </span>
            </infinite-loading>
        </div>
    </div>
</template>

<script>
import {mapActions, mapGetters, mapState} from 'vuex';
import InfiniteLoading from 'vue-infinite-loading';
// import MenuTabs from '@/components/MenuTabs.vue';
import Carousel from '@/components/Carousel.vue';
import NewsList from '@/components/NewsList.vue';
import BLoading from '@/components/BLoading.vue';
import EventBus from '@/event-bus';

// const APP_HEADER_HEIGHT = 52;

export default {
    name: 'home',
    props: {},
    data() {
        return {
            newsFavorListShow: false,
            scrollTops: {},
            showLoading: true
            // menuTabsTop: APP_HEADER_HEIGHT
        };
    },
    components: {
        NewsList,
        InfiniteLoading,
        // MenuTabs,
        Carousel,
        BLoading
    },
    methods: {
        ...mapActions('appShell/appHeader', [
            'setAppHeader'
        ]),
        ...mapActions('appShell/appBottomNavigator', [
            'showBottomNav',
            'activateBottomNav'
        ]),
        ...mapActions('appShell/appSidebar', [
            'disableSwipeOut',
            'enableSwipeOut'
        ]),
        ...mapActions([
            'getNewsList'
        ]),
        async getMoreNews() {
            await this.getNewsList(this.category);
            setTimeout(() => this.$refs.infiniteLoading.$emit('$InfiniteLoading:' + this.loaded), 100);
        }
    },
    computed: {
        ...mapGetters([
            'category',
            'listFromCache',
            'loaded',
            'data',
            'lastListLen'
        ]),
        ...mapState('appShell', [
            'historyPageScrollTop',
            'isPageSwitching'
        ]),
        ...mapState('route', [
            'from'
        ]),
        newsList() {
            if (!this.data[this.category]) {
                this.showLoading = true;
                return [];
            }

            this.showLoading = false;
            return this.data[this.category].news;
        }
        // menuTabsTop() {
        //     *
        //      * https://stackoverflow.com/a/37953806
        //      * 切换动画时，由于父元素应用transform，子元素fixed定位实效，会表现地像absolute
        //      * 因此需要设置top为之前保存的滚动距离

        //     if (this.isPageSwitching) {
        //         return this.$route.path === '/' ? 0 : this.historyPageScrollTop['/'];
        //     }
        //     return APP_HEADER_HEIGHT;
        // }
    },
    watch: {
        category(val, old) {
            this.scrollTops[old] = this.$refs.contentWrapper.scrollTop;
        }
    },
    updated(data) {
        if (this.listFromCache) {
            this.$refs.contentWrapper.scrollTop = this.scrollTops[this.category];
        }
    },
    async asyncData({store, route}) {
        let localTabData = localStorage.getItem('activeTab');
        let category = '推荐';
        if (localTabData) {
            category = localTabData.split('|')[1];
        }
        await store.dispatch('selectTab', category);
    },
    activated() {
        this.setAppHeader({
            show: true,
            title: '百度新闻',
            showMenu: true,
            showBack: false,
            showLogo: false,
            actions: [
                {
                    icon: 'search',
                    route: '/search'
                }
            ]
        });

        this.enableSwipeOut();
        // this.$refs.contentWrapper.scrollTop = this.scrollTops[this.category];
    },
    deactivated() {
        this.disableSwipeOut();
        this.scrollTops[this.category] = this.$refs.contentWrapper.scrollTop;
    }
    // created() {
    //     EventBus.$on('app-page:after-leave', () => {
    //         if (this.$route.path === '/') {
    //             this.menuTabsTop = APP_HEADER_HEIGHT;
    //         }
    //     });
    //     EventBus.$on('app-page:before-enter', () => {
    //         this.menuTabsTop = this.$route.path === '/' ? 0 : this.historyPageScrollTop['/'];
    //     });
    // }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom


.carousel
    height 232px
    width 100%

.content-wrapper
    padding-top: 40px

// .loading-spiral
//     border-color: $theme.primary !important

</style>



